body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.countdown-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 50px;
}

.countdown-wrapper.engagement-photos .wedding {
    font-size: 2rem;
}

.countdown-wrapper.engagement-photos:hover {
    cursor: pointer;
}
.timer {
    display: flex;
    color: white;
}

.timer-element-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0px 10px;
}

.timer-element-wrapper .timer-element-amount {
    font-size: 1.5rem;
    font-weight: bold;
}

.timer-element-wrapper .timer-element-text {
    text-transform: uppercase;
    font-size: 0.8rem;
}
.home-wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    z-index: 10;
  }
  
  .center-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
  }
  
  .center-wrapper .wedding {
    position: relative;
    z-index: 10;
    font-size: 2.5rem;
    color: white;
    letter-spacing: 12px;
    margin-bottom: 15px;
  }

  .home-wrapper .bride-groom-container {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .home-wrapper .bride-groom-names-wrapper {
    flex: 0 0 200px;
    position: relative;
    z-index: 10;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    background-color: rgba(255, 255, 255, 0.85);
    color: #4d4f53;
    border-radius: 100px;
    height: 200px;
    width: 200px;
    box-shadow: 0 3px 6px 3px rgba(0,0,0,.2), 0 2px 2px 0 rgba(0,0,0,.14), 0 1px 5px 0 rgba(0,0,0,.12);
    text-transform: uppercase;
    letter-spacing: 3px;
  }
  
  .home-wrapper .bride-groom-names-wrapper .and {
    font-size: 1rem;
  }
html, body, #root {
  height: 100%;
  overflow: hidden;
}

.hack {
  display: none;
}

html {
  transform: translate3d(0,0,0);
  -webkit-transform: translate3d(0,0,0);
}

.App {
  height: 100%;
  flex-direction: column;
  background-image: url(/wedding/static/media/home-background.5782827c.jpg);
  background-size: cover;
  background-position: center;
  position: static;
  overflow: auto;
  overflow-x: hidden;
}

.fade-in{
  animation: animationFrames ease 1s;
  animation-iteration-count: 1;
  transform-origin: 50% 50%;
  animation-fill-mode:forwards; /*when the spec is finished*/
  -webkit-animation: animationFrames ease 1s;
  -webkit-animation-iteration-count: 1;
  -webkit-transform-origin: 50% 50%;
  -webkit-animation-fill-mode:forwards; /*Chrome 16+, Safari 4+*/ 
  -moz-animation: animationFrames ease 1s;
  -moz-animation-iteration-count: 1;
  -moz-transform-origin: 50% 50%;
  -moz-animation-fill-mode:forwards; /*FF 5+*/
  -o-animation: animationFrames ease 1s;
  -o-animation-iteration-count: 1;
  -o-transform-origin: 50% 50%;
  -o-animation-fill-mode:forwards; /*Not implemented yet*/
  -ms-animation: animationFrames ease 1s;
  -ms-animation-iteration-count: 1;
  -ms-transform-origin: 50% 50%;
  -ms-animation-fill-mode:forwards; /*IE 10+*/
}

@keyframes animationFrames{
  0% {
    opacity:0;
    -webkit-transform:  translate(0px,-25px)  ;
            transform:  translate(0px,-25px)  ;
  }
  100% {
    opacity:1;
    -webkit-transform:  translate(0px,0px)  ;
            transform:  translate(0px,0px)  ;
  }
}

@-webkit-keyframes animationFrames {
  0% {
    opacity:0;
    -webkit-transform:  translate(0px,-25px)  ;
  }
  100% {
    opacity:1;
    -webkit-transform:  translate(0px,0px)  ;
  }
}
@media screen and (max-width: 800px) {
    .center-wrapper {
      flex-direction: column;
      align-self: stretch;
      justify-content: center;
    }

    .countdown-wrapper {
        margin: 20px 50px;
    }
  }

@media screen and (max-width: 400px) {
    .center-wrapper .wedding {
        font-size: 2rem;
    }
}
.content {
    min-height: 100%;
    background-size: cover;
    background-position: center;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
}

.engagement-content {
    background-image: url(/wedding/static/media/test-background.f56ee69e.jpg);
}

.wedding-content {
    /* background-image: url("images/fall-background.jpg"); */
    background-attachment: fixed;
    transform: translate3d(0,0,0);
    -webkit-transform: translate3d(0,0,0);
}

.content-title {
    font-size: 2rem;
    /* font-weight: bold; */
    letter-spacing: 4px;
    margin-top: 30px;
    text-transform: uppercase;
    margin-bottom: 30px;
    flex: 0 1 auto;
    text-align: left;
}

.content-title.wedding-title {
    font-size: 3rem;
    text-shadow: 2px 2px rgba(0, 0, 0, 0.55);
    color: white;
}
.photos-page {
    height: 100%;
    overflow: auto;
    display: flex;
    flex-direction: column;
    background-color: whitesmoke;
}

.home-nav {
    position: relative;
    top: -8px;
    text-transform: uppercase;
    letter-spacing: 1px;
    background-color: transparent;
    border: none;
    font-weight: 200;
    margin-right: 10px;
    outline: none;
    color: white;
}

.home-nav:hover {
    cursor: pointer;
}

.photos-header {
    text-transform: uppercase;
    letter-spacing: 12px;
    font-size: 2.5rem;
    background-color: #4d4f53;
    color: white;
    padding: 10px;
    box-shadow: 0 3px 1px -2px rgba(0,0,0,.2), 0 2px 2px 0 rgba(0,0,0,.14), 0 1px 5px 0 rgba(0,0,0,.12);
}

.photos-content {
    padding: 10px 0px;
}

.my-pic-wrapper {
    display: inline-block;
    width: 235px;
    position: relative;
}

.my-pic {
    width: 100%;
    max-width: 200px;
    margin: 20px 15px;
    box-shadow: 0 3px 1px -2px rgba(0,0,0,.2), 0 2px 2px 0 rgba(0,0,0,.14), 0 1px 5px 0 rgba(0,0,0,.12);
    transition: width 0.5s, -webkit-transform 0.3s;
    transition: transform 0.3s, width 0.5s;
    transition: transform 0.3s, width 0.5s, -webkit-transform 0.3s;
}

