.photos-page {
    height: 100%;
    overflow: auto;
    display: flex;
    flex-direction: column;
    background-color: whitesmoke;
}

.home-nav {
    position: relative;
    top: -8px;
    text-transform: uppercase;
    letter-spacing: 1px;
    background-color: transparent;
    border: none;
    font-weight: 200;
    margin-right: 10px;
    outline: none;
    color: white;
}

.home-nav:hover {
    cursor: pointer;
}

.photos-header {
    text-transform: uppercase;
    letter-spacing: 12px;
    font-size: 2.5rem;
    background-color: #4d4f53;
    color: white;
    padding: 10px;
    box-shadow: 0 3px 1px -2px rgba(0,0,0,.2), 0 2px 2px 0 rgba(0,0,0,.14), 0 1px 5px 0 rgba(0,0,0,.12);
}

.photos-content {
    padding: 10px 0px;
}

.my-pic-wrapper {
    display: inline-block;
    width: 235px;
    position: relative;
}

.my-pic {
    width: 100%;
    max-width: 200px;
    margin: 20px 15px;
    box-shadow: 0 3px 1px -2px rgba(0,0,0,.2), 0 2px 2px 0 rgba(0,0,0,.14), 0 1px 5px 0 rgba(0,0,0,.12);
    transition: transform 0.3s, width 0.5s;
}

