.my-pic.vertical {
    max-height: 200px;
    max-width: 125px;
}

.my-pic:hover {
    cursor: pointer;
    transform: scale(1.1);
}

.sweet-loading {
    position: fixed;
    z-index: 99999;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.5);
}

.sweet-loading.hidden {
    display: none;
}

.full-pic {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 999;
    background-color: rgba(0, 0, 0, 0.85);
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}

.close-full-mode {
    position: absolute;
    top: 10px;
    right: 25px;
    font-size: 28px;
    color: white;
    z-index: 999999;
}

.close-full-mode:hover {
    cursor: pointer;
    color: whitesmoke;
}

.nav-button {
    position: absolute;
    height: 100%;
    width: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.previous-pic {
    left: 0;
}

.next-pic {
    right: 0;
}

.nav-icon {
    color: white;
    font-size: 32px;
}

.nav-icon:hover {
    cursor: pointer;
    color: whitesmoke;
}

.footer {
    display: flex;
    justify-content: center;
    align-items: center;
}
.load-more {
    flex: 1;
    text-transform: uppercase;
    font-size: 16px;
    letter-spacing: 2px;
    box-sizing: border-box;
    min-height: 30px;
    max-width: 500px;
    margin: 15px;
    background-color: lightgrey;
    border: 1px solid lightgray;
    box-shadow: 0 3px 1px -2px rgba(0,0,0,.2), 0 2px 2px 0 rgba(0,0,0,.14), 0 1px 5px 0 rgba(0,0,0,.12);
}