.countdown-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 50px;
}

.countdown-wrapper.engagement-photos .wedding {
    font-size: 2rem;
}

.countdown-wrapper.engagement-photos:hover {
    cursor: pointer;
}
.timer {
    display: flex;
    color: white;
}

.timer-element-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0px 10px;
}

.timer-element-wrapper .timer-element-amount {
    font-size: 1.5rem;
    font-weight: bold;
}

.timer-element-wrapper .timer-element-text {
    text-transform: uppercase;
    font-size: 0.8rem;
}